import React, { useState, useEffect } from "react";
import axios from "axios";
const DataUrl = "http://localhost:3100/ele";

function App() {
  const [List, setList] = useState([]);
  useEffect(() => {
    const fetchData = async () => {
      const response = await axios.get(DataUrl);
      setList(response.data);
    };
    fetchData();
  }, []);

  return (
    <>
      <h1>WebデザインB(9週)</h1>
      <h2>やることリスト</h2>
      <ul>
        {List.map((ele) => (
          <li key={ele.id}>
            {ele.content}（{ele.done ? "完了" : "未完了"}）
          </li>
        ))}
      </ul>
    </>
  );
}

export default App;
